form, input, button {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 18px;
}

form {
  flex-direction: row;
  margin: 10px;
  display: flex;
}

label {
  flex-direction: column;
  margin: 5px;
  display: flex;
}

input {
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 2px;
  padding: 3px;
}

button {
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 2px;
  margin: 32px 0 15px 5px;
  padding: 3px 10px;
}

